*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.stars {
  display: flex;
}

.stars__item {
  background-image: url("./images/star.svg");
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 4px;
  width: 15px;
  height: 15px;
}

.stars--1 .stars__item:nth-child(1),
.stars--2 .stars__item:nth-child(-n + 2),
.stars--3 .stars__item:nth-child(-n + 3),
.stars--4 .stars__item:nth-child(-n + 4),
.stars--5 .stars__item:nth-child(-n + 5) {
  background-image: url("./images/star-active.svg");
}
